@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz@6..12&family=Playfair+Display:ital,wght@0,600;0,700;0,800;1,400;1,500;1,800&display=swap');

#background{
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#quote-box{
  background: #fff;
  width: 40rem;
  margin: 0 auto ;
  padding: 3rem;
  border-radius: 5px ;
  height: 300px;
}

#text{
  font-size: 2rem;
  margin-bottom: 1.5rem;
}
#author{
  display: flex;
  justify-content: flex-end;
  font-size: 1.2rem;
}
#buttons{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 2rem;
  border: 0;
  background: #fff;
  width: 100%;
}

.social-media{
  display: flex;
}
.social-media > a{
  margin-right: 0.5rem;
  background-color: #0059ff;
  padding: .3rem .5rem;
  cursor: pointer;
  border-radius: 3px;
}

.social-media > a:hover{
  opacity: .9;
}
.social-media > a img{
  width: 1.5rem;
}

#new-quote{
  background: #0059ff;
  border: none;
  color: #fff;
  padding: .7rem 1rem;
  font-size: .9rem;
  font-weight: bold;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
  align-self: flex-end;
}

#new-quote:hover{
  opacity: .9;
}


